* {
	margin: 0;
	padding: 0;
}
ul {
	list-style-type: none;
}
.nav {
	background: rgba(0,0,0,0) !important; 
  	position: absolute;
  	top: 0;
  	width: 100%;
	display: flex;
	justify-content: space-around;
	border-bottom: 1px solid #3b2c3b;
}

.cont1 {
	font-family: "Open Sans";
	background-color: #2e3b4e;
}
.cont1:after{
	    content: "&nbsp";
    width: 80px !important;
    height: 80px !important;
    background-color: #2e3b4e;
    padding: 20px;
    color: transparent;
    border-radius: 58%;
    /* text-align: center; */ 
    margin: -60px;
    display: flex;
    position: absolute;
    left: 50%;
    z-index: 1;
}
.nav {
	font-family: "Open Sans";
}
.cont2 {
	font-family: "Open Sans";
}
.cont3 {
