}



.more {
	z-index: 2;
	padding: 20px 0;
	display: none;
	position: relative;
	text-decoration: none;
	color: #fff;
}
.nav_loc_sm {
	display: none;
	position: absolute;
	background-color: #3b2c3b;
	color: #000;
	top: 48px;
	left: -52px;
}
.nav_loc_sm .nav_loc_items {
	text-decoration: none;
	border: none;
	padding: 9px 70px 9px 25px;
}
.nav_loc_sm .nav_loc_items:hover {
	background-color: #000;
	color: #3b2c3b;
}
.more:hover .nav_loc_sm {
	display: block;
}
.nav_sm_form {
	display: none;
}
.nav_loc_item2 {
	color: #fff;
	text-decoration: none;
}
.nav_parth {
	margin: 30px;
}
.nav_span {
	font-family: "Bellota";
	color: #deddde;
}
.nav_loc {
	margin: 15px;
	display: flex;
}
.form {
	margin: 15px;
	position: relative;
}
.smform {
	margin: 15px;
}
.smform .smforminput {
	padding: 5px;
	border-radius: 75px;
	border: none;
}
.smform .smsearch {
	position: absolute;
	background-color: #fff;
	top: 50%;
    right: 10%;
    transform: translate(50%, -50%);
}
.form .forminput {
	padding: 10px;
	border-radius: 75px 0 0 75px;
	border: none;
}
.form .search {
	padding: 10px;
	border: none;
	background-color: #fff;
	border-radius: 0 75px 75px 0;
}
.nav_loc_item {
	color: #fff;
	text-decoration: none;

}
.nav_loc_item:hover {
	border-bottom: 2px solid #fff;
}
.logoimg {
	width: 100%;
 	height: 100%;
 	background-size: cover;
}







.logo {
	font-family: "Open Sans";
	text-align: right;
	position: absolute;
	top: 250px;
	color: #fff;
	left: 100px;
}
.logobtn {
	color: #fff;
	margin: 20px 0 0 0;
	background-color: #8824cd;
	padding: 10px 30px;
	border: none; 
	border-radius: 40px;
	font-size: 14px;
	font-weight: 700;
}
.logotext {
	color: #e2dbde;
	font-size: 9px;
	margin: 5px 10px;

}




 




